<template>
	<div id="back">
    <!--<div class="applyTable">-->
    <div id="adjShow">
      <div id="adjTop1">
        <span>{{year}}{{semester}}</span>
        <span>{{week}}</span>
        <span>21700</span>
      </div>
      <div id="adjTop2">
        <span>{{year}}{{semester}}{{courseNum}}</span>
      </div>
      <div class="adjShowDiv">
        <table class="table table-hover table-bordered" cellspacing="1">
          <thead >
          <tr>
            <th width="10%"></th>
            <th width="15%"></th>
            <th width="15%"></th>
            <th width="15%"></th>
            <th width="15%"></th>
            <th width="15%"></th>
            <th width="15%"></th>
          </tr>
          </thead>
          <tbody>
            <tr v-for="(data,index) in tableList">
              <td width="10%">
                <span :id="'show'+index" @click="show(index)"></span>
              </td>
              <td width="15%" v-text="index+1"></td>
              <td width="15%" v-text="data.className"></td>
              <td width="15%" v-text="data.courseName"></td>
              <td width="15%" v-text="data.courseId"></td>
              <td width="15%" v-text="data.teachername"></td>
              <td width="15%" v-text="data.originTime"></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-show="seenS" id="adjSel">
      <div id="adjTop3"><spam>:</spam></div>
      <table>
        <tbody>
            <tr>
          <td class="designation" width="8%">
            <span></span>
          </td>
          <td class="choice" width="24%">
            <select @change="choose(selected1,selected4)" v-model="selected1">
              <option></option>
              <option v-for="option1 in options1">
                {{ option1 }}
              </option>
            </select>
          </td>
          <td class="designation" width="8%">
            <span></span>
          </td>
          <td class="choice" width="24%">
            <select  v-model="selected2">
              <option></option>
              <option v-for="option2 in options2" >
                {{ option2}}
              </option>
            </select>
          </td>
          <td class="designation" width="8%">
            <span></span>
          </td>
          <td class="choice" width="24%">
            <select v-model="selected3" >
              <option></option>
              <option v-for="option3 in options3" >
                {{ option3 }}
              </option>
            </select>
          </td>
            </tr>
            <tr>
          <td class="designation" width="8%">
            <span></span>
          </td>
          <td class="choice" width="24%">
            <select v-model="selected4" @change="choose(selected1,selected4)" >
              <option></option>
              <option v-for="option4 in options4" >
                {{ option4 }}
              </option>
            </select>
          </td>

          <td class="designation" width="8%">
            <span></span>
          </td>
          <td class="choice" width="24%">
            <textarea v-model="message2"></textarea>
          </td>
            </tr>
          </tbody>
      </table>
      <button  class="am-btn am-btn-success am-radius" @click="saveSel(selected1,selected2,selected3,selected4,message2)"></button>
      <button class="am-btn am-btn-success am-radius" @click="cancel"></button>
    </div>
	<!--</div>-->
    </div>
</template>

<script>
// import bootPage from './components/bootPage'

export default {
	name: 'nameUp',
	data () {
		return {
			year:'',
      semester:'',
			week:'',
			courseNum:'',
			seenS:false,
			tableList: [],
			selected1: '',
      selected2: '',
      selected3: '',
      selected4: '',
			options1: [],
			options2: [],
			options3: [],
			options4: []
		}
	},
  beforeMount:function(){
    this.$http.post('./makeUpLessionApplication.action',{},
      {"Content-Type":"application/json"}).then(function (response) {
        console.log(response);
        this.year = response.body.year;
        this.semester = response.body.semester;
        this.week = response.body.week;
        this.courseNum = response.body.courseNum;
        this.tableList = response.body.lessonList;
      },
      function(error){
        console.log("error:");
        console.log(error);
      });
  },
    methods:{
      show:function(index){
        this.seenS=true;
        this.$http.post('./makeUpLessionApplication/application-button.action',{
          "teachername": this.tableList[index].teachername,
          "courseId":this.tableList[index].courseId,
          "className":this.tableList[index].className,
          "originTime":this.tableList[index].originTime,
          "courseName":this.tableList[index].courseName
        },{"Content-Type":"application/json"}).then(function (response) {
            console.log(":");
            console.log(response.body);
            for(var x=0;x<response.body.selectiveWeekSet.length;x++){
              this.options1.push(response.body.selectiveWeekSet[x]);}
            for(var n=0;n<response.body.selectiveClassroomSet.length;n++){
              this.options4.push(response.body.selectiveClassroomSet[n]);}
          },
          function(error){
            console.log("error:");
            console.log(error);
          });
      },
      choose:function(sel1,sel4){
        if(sel1!=""&&sel4!=""){
         this. $http.post('./makeUpLessionApplication/application-select.action',{
           "selectedWeek":sel1,
           "selectedClassroom":sel4
         },{"Content-Type":"application/json"}).then(function(response){
           for(var i=0;i<response.body.selectiveList.length;i++){
             this.options2.push(response.body.selectiveList[i][0]);
             this.options3.push(response.body.selectiveList[i][1]);
           }
         },function(error){
           console.log("error:");
           console.log(error);
         })
        }
      },
      saveSel:function(sele1,sele2,sele3,sele4,message){
        this.$http.post('./makeUpLessionApplication/application-submit.action',{
          "mediationReason":message,
          "selected1":sele1,
          "selected2":sele2,
          "selected3":sele3,
          "selected4":sele4
        },{"Content-Type":"application/json"}).then(function (response) {
            if(response.result=="1")
            {alert("success!")}
            console.log(":");
            console.log(response.body);
          },
          function(error){
            console.log("error:");
            console.log(error);
          });
        this.seenS=false;
      },
      cancel:function(){
        location.reload();
      }
    }


}
</script>

<style scoped>
  @import '../../../../assets/css/external.css';
  #back{
    background-color: #f3f3f3;
    padding-top:0.1rem;
  }
#adjTop1{
	background-color: white;
	position: relative;
	height:2rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0.3rem;

}       /*1*/
#adjTop2{
	background-color: white;
	position: relative;
	height:2rem;
	display: flex;
	align-items: center;
	padding: 0.3rem;
}       /*2*/
#adjTop3{
	background-color: white;
	position: relative;
	height:2rem;
	display: flex;
	align-items: center;
	padding: 0.3rem;
}       /*3*/
#adjShow {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 1.5rem;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
	/*background-color: #f3f3f3;*/



}

.adjShowDiv {
	background-color: white;
	border: thin solid lightgrey;
	font-size: 1rem;
	position: relative;
	top:1rem;
   margin-bottom: 2rem;

}
table{
  border-collapse:collapse ;
  width:100%
}

.table th {
	font-size: 0.9rem;
	font-weight: bold;
	background-color: white;
	height: 2rem;
	border:thin solid lightgrey;
}    /**/
.table td {
	font-size: 0.5rem;
	background-color: white;
	height: 2rem;
	border:thin solid lightgrey;
}    /**/
 #adjSel {
	 text-align: center;
	 margin: 20px;
	margin-top: 0.2rem;
	background: white;
	height: 12rem;
}     /**/
#adjSel table {
	width: 100%;
}
.designation {
	text-align: right;
}
.choice {
	text-align: left;
}
#adjSel tr {
	height: 2.4rem;
}
#adjSel td {
	position: relative;
}
textarea{
		border: 0.1rem solid #d4d4d9;
}
button{
   width:5.6rem;
  margin:0.7rem;
}
select{
	border: 0.1rem solid #d4d4d9;
	        border-radius: 0.7rem;
	        outline: none;
	        padding: 0.3rem 0.5rem;
	        margin: 0.1rem;
	        font-size: 0.8rem;
        }
@media screen and (max-width:1023px) {
	html {
		font-size: 9px;
	}
}



</style>



